import React, { Component } from 'react';

class Nav extends Component {
  constructor(props) {
    super(props);
    this.state = {addClass: false}
  }
  toggle() {
    this.setState({addClass: !this.state.addClass});
  }
  render() {
    let boxClass = ["box"];
    let logoClass = ["small"];
    if(this.state.addClass) {
      boxClass.push('green');
      logoClass.push('small');
    }
    return(
         <nav>

  <input id="nav" type="checkbox" />

  <label for="nav">
    <strong>Menu</strong>
    <b><i></i><i></i><i></i></b>
  </label>

  <menu>
    <li><a href="#"><ruby>Development <rp>(</rp><rt>デベロプメント</rt><rp>)</rp></ruby></a></li>
    <li><a href="#"><ruby>Design <rp>(</rp><rt>デザイン</rt><rp>)</rp></ruby></a></li>
    <li><a href="#"><ruby>Print <rp>(</rp><rt>プリント</rt><rp>)</rp></ruby></a></li>
    <li><a href="#"><ruby>About <rp>(</rp><rt>アバウト</rt><rp>)</rp></ruby></a></li>
  </menu>

</nav>
    );
  }
}

export default Nav;