import React, {Component} from 'react';
import logo from './images/jamiedesignedfinal2.png';
import Portfolio from './components/Portfolio';
import AddTripButton from './components/test';
import Button from './components/Button';
import Nav from './components/Nav';
import About from './components/AboutMe';
import './styles/App.css';
import './styles/Nav.css';
import './styles/portfolio.css';
import './styles/About.css';

class App extends Component {

    constructor(props) {
        super(props)
        this.state = {isEmptyState: true}
    }

    triggerAddTripState = () => {
        this.setState({
            ...this.state,
            isEmptyState: false,
            isAddTripState: true
        })
    }

    render() {

        return (
            <div className="App">
                {this.state.isEmptyState &&
                <header className="home-screen">
                    <div className="App-header section-padding">
                        <img src={logo} className="App-logo" alt="logo"/>
                    </div>
                </header>}

                {this.state.isAddTripState &&
                <header>
                    <div className="lang">
                    <a><h3>ENGLISH</h3></a>
                        <h3> | </h3>
                    <a><h3>日本語</h3></a>
                    </div>
                    <div className="App-header section-padding">
                        <a href="/"> <img src={logo} className="App-logo-small" alt="logo"/></a>
                    </div>
                    <Nav/>

                </header>}

                {this.state.isAddTripState && <About/>}

                {/*HOME SCREEN FOOTER*/}
                {this.state.isEmptyState && <footer className="footer-home-screen">
                    <div className="cities section-padding">
                        <ruby>Tokyo <rp>(</rp>
                            <rt>トーキョー</rt>
                            <rp>)</rp>
                        </ruby>
                        <h4>・</h4>
                        <ruby>Dubai <rp>(</rp>
                            <rt>ドゥバイ</rt>
                            <rp>)</rp>
                        </ruby>
                        <h4>・</h4>
                        <ruby>Warwick <rp>(</rp>
                            <rt>ウォリック</rt>
                            <rp>)</rp>
                        </ruby>
                    </div>

                    <div>
                        {this.state.isEmptyState && <AddTripButton addTrip={this.triggerAddTripState}/>}

                    </div>

                    {/*<Button />*/}


                </footer>
                }


                {/*STANDARD SCREEN FOOTER*/}
                {this.state.isAddTripState && <footer className="footer-standard">
                    <div className="cities section-padding">
                        <ruby>Tokyo <rp>(</rp>
                            <rt>トーキョー</rt>
                            <rp>)</rp>
                        </ruby>
                        <h4>・</h4>
                        <ruby>Dubai <rp>(</rp>
                            <rt>ドゥバイ</rt>
                            <rp>)</rp>
                        </ruby>
                        <h4>・</h4>
                        <ruby>Warwick <rp>(</rp>
                            <rt>ウォリック</rt>
                            <rp>)</rp>
                        </ruby>
                    </div>
                    <h3>hello@jamiedesigned.com</h3>
                </footer>}
            </div>
        );
    }

}

export default App;
